import publicHead from "@/components/headBack/head.vue";
import contractText from "@/assets/script/contract";
import dowwnTime from "@/components/downTime/downtime.vue";
let bigNum = require('bignumber.js')
export default{
    data() {
        return {
            // 
            cultLen: 0,
            // 
            currCultLen: 0,
            // 
            cultivatNum: 0,
            // 
            surplusNum: 0,
            // 
            inCommMoney: 0,
            // 
            inputCultivat: "",
            // 
            intiveAddres: "",
            // 
            showcultivat: false,
            // 
            intveTime: null
        }
    },

    async mounted() {
        await this.getCultivatLength();
        this.getCultivatNumb();
        this.getParseAddres();
        this.getSdrNumber();
        this.intveTime = setInterval(()=>{
            this.getSdrNumber();
        },3000);
    },

    beforeDestroy() {
        clearInterval(this.intveTime);
    },

    methods: {
        // Length
        getCultivatLength() {
            let _this = this;
            let loding = this.$createToast({
                txt: "",
                time: 0,
                mask: true,
                type: 'loading',
            }).show();
            return new Promise((resolve,reject)=>{
                let contractObj = this.getContract("FarmingPool");
                contractObj.methods.capitalLength(localStorage.getItem("address")).call().then((res)=>{
                    _this.cultLen = res;
                    loding.hide();
                    resolve();
                }).catch(()=>{
                    loding.hide();
                })
            });
        },

        // 
        getCultivatNumb() {
            if(this.cultLen == 0) {
                return;
            }
            if(this.currCultLen == this.cultLen) {
                this.currCultLen = 0;
                return;
            }
            let contractObj = this.getContract("FarmingPool");
            let _this = this;
            contractObj.methods.capitals(this.$store.state.addres,this.currCultLen).call().then((res)=>{
                _this.cultivatNum += parseFloat(_this.accuracyDiv(res.amount));
                _this.surplusNum += parseFloat(_this.decimalAction(_this.accuracyDiv(res.balance),6));
                _this.currCultLen += 1;
                _this.getCultivatNumb();
            });
        },

        // SDR
        getSdrNumber() {
            let contractObj = this.getContract("FarmingPool");
            let _this = this;
            contractObj.methods.rewarding(this.$store.state.addres).call().then((res)=>{
                _this.inCommMoney = _this.decimalAction(_this.toFixedNum(_this.accuracyDiv(res)),10);
                
            });
        },

        // 
        getParseAddres() {
            let contractObj = this.getContract("FarmingPool");
            let _this = this;
            contractObj.methods.inviter(this.$store.state.addres).call().then((res)=>{
                if(res != "0x0000000000000000000000000000000000000000") {
                    _this.intiveAddres = res;
                }
            });
        },

        // 
        confirmCultivatAction() {
            if(!this.inputCultivat || parseFloat(this.inputCultivat) < 100 || parseFloat(this.inputCultivat)>10000) {
                this.$createToast({
                    txt: this.$t('m.text225'),
                    time: 1500,
                    mask: true,
                    type: 'warn',
                }).show();
                return;
            }
            if(!this.intiveAddres) {
                this.$createToast({
                    txt: this.$t('m.text226'),
                    time: 1500,
                    mask: true,
                    type: 'warn',
                }).show();
                return;
            }
            let contractObj = this.getContract("USDT");
            let _this = this;
            let approve = contractObj.methods.approve(contractText['FarmingPool'].addres,bigNum(this.accuracyMul(this.inputCultivat)));
            let loding = this.$createToast({
                txt: "",
                time: 0,
                mask: true,
                type: 'loading',
            }).show();
            approve.send({
                from: localStorage.getItem("address")
            }).then((res)=>{
                _this.showcultivat = false;
                loding.hide();
                _this.startCultivatAction(_this.inputCultivat,_this.intiveAddres);
                _this.inputCultivat = "";
                _this.intiveAddres = "";
            }).catch(()=>{
                _this.$createToast({
                    txt: _this.$t('m.text227'),
                    time: 1500,
                    mask: true,
                    type: 'warn',
                }).show();
            });
        },

        // 
        startCultivatAction(moeny,address) {
            let contractObj = this.getContract("FarmingPool");
            let _this = this;
            let start = contractObj.methods.launch(address,this.accuracyMul(moeny));
            let loding = this.$createToast({
                txt: "",
                time: 0,
                mask: true,
                type: 'loading',
            }).show();
            start.send({
                from: localStorage.getItem("address")
            }).then((res)=>{
                loding.hide();
                _this.$createToast({
                    txt: _this.$t('m.text228'),
                    time: 1500,
                    mask: true,
                    type: 'correct',
                }).show();
                setTimeout(()=>{
                    _this.cultivatNum = 0;
                    _this.surplusNum = 0;
                    _this.getCultivatNumb();
                },3000);
            }).catch(()=>{
                loding.hide();
                _this.$createToast({
                    txt: _this.$t('m.text68'),
                    time: 1500,
                    mask: true,
                    type: 'correct',
                }).show();
            });
        },

        // 
        getIncommMone() {
            if(!this.inCommMoney) {
                _this.$createToast({
                    txt: _this.$t('m.text229'),
                    time: 1500,
                    mask: true,
                    type: 'warn',
                }).show();
                return;
            }
            let contractObj = this.getContract("FarmingPool");
            let _this = this;
            let loding = this.$createToast({
                txt: "",
                time: 0,
                mask: true,
                type: 'loading',
            }).show();
            let reward = contractObj.methods.reward();
            reward.send({
                from: localStorage.getItem("address")
            }).then((res)=>{
                loding.hide();
                _this.surplusNum = 0;
                _this.cultivatNum = 0;
                _this.getCultivatNumb();
                _this.$createToast({
                    txt: _this.$t('m.text230'),
                    time: 1500,
                    mask: true,
                    type: 'correct',
                }).show();
            }).catch(()=>{
                loding.hide();
                _this.$createToast({
                    txt: _this.$t('m.text231'),
                    time: 1500,
                    mask: true,
                    type: 'warn',
                }).show();
            });
        },

        // 
        goToQuest() {
            this.$router.push({
                path: "/question"
            })
        },

        // 
        goToComputer() {
            this.$router.push({
                path: "/calculator"
            })
        },

        // 
        showCultivationAction() {
            this.showcultivat = true;
        },

        // 
        closeCultivationAction() {
            this.showcultivat = false;
            this.inputCultivat = "";
            this.intiveAddres = "";
        },
        
        // 
        toFixedNum(x) {
            if (Math.abs(x) < 1.0) {
                var e = parseInt(x.toString().split('e-')[1]);
                if (e) {
                    x *= Math.pow(10,e-1);
                    x = '0.' + (new Array(e)).join('0') + x.toString().substring(2);
                }
              } else {
                var e = parseInt(x.toString().split('+')[1]);
                if (e > 20) {
                    e -= 20;
                    x /= Math.pow(10,e);
                    x += (new Array(e+1)).join('0');
                }
              }
              return x;
        },

    },
    components: {
        publicHead,
        dowwnTime
    }
}